/**
 * Convertit le texte en caract√®res Unicode Mathematical Bold
 * Utilis√© pour le formatage **gras** sur LinkedIn
 * @param {string} text - Le texte √† formater
 * @returns {string} - Le texte format√© en gras Unicode
 */
export function toBold(text) {
  if (!text || typeof text !== 'string') {
    return text;
  }

  // Tables de mapping Unicode Mathematical Bold
  const boldMappings = {
    // Lettres majuscules A-Z ‚Üí ùêÄ-ùêô (U+1D400-U+1D419)
    'A': 'ùêÄ', 'B': 'ùêÅ', 'C': 'ùêÇ', 'D': 'ùêÉ', 'E': 'ùêÑ', 'F': 'ùêÖ', 'G': 'ùêÜ', 'H': 'ùêá',
    'I': 'ùêà', 'J': 'ùêâ', 'K': 'ùêä', 'L': 'ùêã', 'M': 'ùêå', 'N': 'ùêç', 'O': 'ùêé', 'P': 'ùêè',
    'Q': 'ùêê', 'R': 'ùêë', 'S': 'ùêí', 'T': 'ùêì', 'U': 'ùêî', 'V': 'ùêï', 'W': 'ùêñ', 'X': 'ùêó',
    'Y': 'ùêò', 'Z': 'ùêô',
    
    // Lettres minuscules a-z ‚Üí ùêö-ùê≥ (U+1D41A-U+1D433)
    'a': 'ùêö', 'b': 'ùêõ', 'c': 'ùêú', 'd': 'ùêù', 'e': 'ùêû', 'f': 'ùêü', 'g': 'ùê†', 'h': 'ùê°',
    'i': 'ùê¢', 'j': 'ùê£', 'k': 'ùê§', 'l': 'ùê•', 'm': 'ùê¶', 'n': 'ùêß', 'o': 'ùê®', 'p': 'ùê©',
    'q': 'ùê™', 'r': 'ùê´', 's': 'ùê¨', 't': 'ùê≠', 'u': 'ùêÆ', 'v': 'ùêØ', 'w': 'ùê∞', 'x': 'ùê±',
    'y': 'ùê≤', 'z': 'ùê≥',
    
    // Chiffres 0-9 ‚Üí ùüé-ùüó (U+1D7CE-U+1D7D7)
    '0': 'ùüé', '1': 'ùüè', '2': 'ùüê', '3': 'ùüë', '4': 'ùüí',
    '5': 'ùüì', '6': 'ùüî', '7': 'ùüï', '8': 'ùüñ', '9': 'ùüó'
  };

  try {
    // Transformer chaque caract√®re
    let result = '';
    for (let i = 0; i < text.length; i++) {
      const char = text[i];
      const boldChar = boldMappings[char];
      result += boldChar || char; // Utiliser le caract√®re gras ou le caract√®re original
    }

    console.log('‚úÖ Texte format√© en gras:', { original: text, bold: result });
    return result;
  } catch (error) {
    console.error('‚ùå Erreur lors du formatage en gras:', error);
    return text; // Fallback vers le texte original
  }
}

/**
 * Convertit le texte en caract√®res Unicode Mathematical Italic
 * Utilis√© pour le formatage *italique* sur LinkedIn  
 * @param {string} text - Le texte √† formater
 * @returns {string} - Le texte format√© en italique Unicode
 */
export function toItalic(text) {
  if (!text || typeof text !== 'string') {
    return text;
  }

  // Tables de mapping Unicode Mathematical Italic
  const italicMappings = {
    // Lettres majuscules A-Z ‚Üí ùê¥-ùëç (U+1D434-U+1D44D)
    'A': 'ùê¥', 'B': 'ùêµ', 'C': 'ùê∂', 'D': 'ùê∑', 'E': 'ùê∏', 'F': 'ùêπ', 'G': 'ùê∫', 'H': 'ùêª',
    'I': 'ùêº', 'J': 'ùêΩ', 'K': 'ùêæ', 'L': 'ùêø', 'M': 'ùëÄ', 'N': 'ùëÅ', 'O': 'ùëÇ', 'P': 'ùëÉ',
    'Q': 'ùëÑ', 'R': 'ùëÖ', 'S': 'ùëÜ', 'T': 'ùëá', 'U': 'ùëà', 'V': 'ùëâ', 'W': 'ùëä', 'X': 'ùëã',
    'Y': 'ùëå', 'Z': 'ùëç',
    
    // Lettres minuscules a-z ‚Üí ùëé-ùëß (U+1D44E-U+1D467)
    'a': 'ùëé', 'b': 'ùëè', 'c': 'ùëê', 'd': 'ùëë', 'e': 'ùëí', 'f': 'ùëì', 'g': 'ùëî', 'h': 'ùíâ',
    'i': 'ùëñ', 'j': 'ùëó', 'k': 'ùëò', 'l': 'ùëô', 'm': 'ùëö', 'n': 'ùëõ', 'o': 'ùëú', 'p': 'ùëù',
    'q': 'ùëû', 'r': 'ùëü', 's': 'ùë†', 't': 'ùë°', 'u': 'ùë¢', 'v': 'ùë£', 'w': 'ùë§', 'x': 'ùë•',
    'y': 'ùë¶', 'z': 'ùëß'
    
    // Note: Les chiffres n'ont pas d'√©quivalent italique dans Unicode Mathematical
    // Ils restent en forme normale
  };

  try {
    // Transformer chaque caract√®re
    let result = '';
    for (let i = 0; i < text.length; i++) {
      const char = text[i];
      const italicChar = italicMappings[char];
      result += italicChar || char; // Utiliser le caract√®re italique ou le caract√®re original
    }

    console.log('‚úÖ Texte format√© en italique:', { original: text, italic: result });
    return result;
  } catch (error) {
    console.error('‚ùå Erreur lors du formatage en italique:', error);
    return text; // Fallback vers le texte original
  }
}

/**
 * Ajoute combining underline aux caract√®res
 * Utilis√© pour le formatage soulign√© sur LinkedIn
 * @param {string} text - Le texte √† formater
 * @returns {string} - Le texte format√© avec soulignement Unicode
 */
export function toUnderline(text) {
  if (!text || typeof text !== 'string') {
    return text;
  }

  // Approche optimale : Mathematical Monospace + Combining Underline
  // Comme dans ton exemple : ùöÑÃ≤ùöóÃ≤ùöçÃ≤ùöéÃ≤ùöõÃ≤ùöïÃ≤ùöíÃ≤ùöóÃ≤ùöéÃ≤
  const monospaceMap = {
    // Lettres majuscules A-Z ‚Üí Mathematical Monospace (U+1D670-U+1D689)
    'A': 'ùô∞', 'B': 'ùô±', 'C': 'ùô≤', 'D': 'ùô≥', 'E': 'ùô¥', 'F': 'ùôµ', 'G': 'ùô∂', 'H': 'ùô∑',
    'I': 'ùô∏', 'J': 'ùôπ', 'K': 'ùô∫', 'L': 'ùôª', 'M': 'ùôº', 'N': 'ùôΩ', 'O': 'ùôæ', 'P': 'ùôø',
    'Q': 'ùöÄ', 'R': 'ùöÅ', 'S': 'ùöÇ', 'T': 'ùöÉ', 'U': 'ùöÑ', 'V': 'ùöÖ', 'W': 'ùöÜ', 'X': 'ùöá',
    'Y': 'ùöà', 'Z': 'ùöâ',
    
    // Lettres minuscules a-z ‚Üí Mathematical Monospace (U+1D68A-U+1D6A3)
    'a': 'ùöä', 'b': 'ùöã', 'c': 'ùöå', 'd': 'ùöç', 'e': 'ùöé', 'f': 'ùöè', 'g': 'ùöê', 'h': 'ùöë',
    'i': 'ùöí', 'j': 'ùöì', 'k': 'ùöî', 'l': 'ùöï', 'm': 'ùöñ', 'n': 'ùöó', 'o': 'ùöò', 'p': 'ùöô',
    'q': 'ùöö', 'r': 'ùöõ', 's': 'ùöú', 't': 'ùöù', 'u': 'ùöû', 'v': 'ùöü', 'w': 'ùö†', 'x': 'ùö°',
    'y': 'ùö¢', 'z': 'ùö£',
    
    // Chiffres 0-9 ‚Üí Mathematical Monospace (U+1D7F6-U+1D7FF)
    '0': 'ùü∂', '1': 'ùü∑', '2': 'ùü∏', '3': 'ùüπ', '4': 'ùü∫', '5': 'ùüª', '6': 'ùüº', '7': 'ùüΩ', '8': 'ùüæ', '9': 'ùüø'
  };

  // Combining underline Unicode (U+0332)
  const COMBINING_UNDERLINE = '\u0332';

  try {
    let result = '';
    for (let i = 0; i < text.length; i++) {
      const char = text[i];
      const monospaceChar = monospaceMap[char];
      
      if (monospaceChar) {
        // Caract√®re monospace + combining underline
        result += monospaceChar + COMBINING_UNDERLINE;
      } else if (char === ' ') {
        result += char; // Garder les espaces normaux
      } else {
        // Pour les caract√®res non mapp√©s, utiliser le caract√®re original + underline
        result += char + COMBINING_UNDERLINE;
      }
    }

    console.log('‚úÖ Texte format√© avec Mathematical Monospace + Underline:', { 
      original: text, 
      underlined: result,
      method: 'monospace_with_combining_underline'
    });
    return result;
  } catch (error) {
    console.error('‚ùå Erreur lors du formatage soulign√©:', error);
    return text; // Fallback vers le texte original
  }
}

